<script>
export default {
  name: 'the-token-popup',

  props: {
    toggleTokenPopupShow: Function,
    isTokenPopupShow: Boolean,
  },

  watch: {
    isTokenPopupShow() {
      if (this.isTokenPopupShow) {
        this.$nextTick().then(() => document.body.classList.add('popup-open'));
      } else {
        this.$nextTick().then(() => document.body.classList.remove('popup-open'));
      }
    }
  }
};
