<template>
  <div class="search-input">
     <label class="search-input__button-icon" :for="id">
        <svg width="17" height="17" xmlns="http://www.w3.org/2000/svg"><defs><linearGradient x1="0%" y1="0%" y2="100%" id="a"><stop stop-color="#B204CE" offset="0%"/><stop stop-color="#2B91E5" offset="100%"/></linearGradient></defs><text transform="translate(-19 -14)" fill="url(#a)" fill-rule="evenodd" font-family="Ionicons" font-size="22" font-weight="400"><tspan x="19.5" y="31"></tspan></text></svg>
      </label>
    <input
      type="text"
      class="search-input__input"
      v-bind="$attrs"
      :id="id"
      @input="updateValue($event.target.value)"
      :value="value"
    >
  </div>
</template>


<script>
export default {
  name: 'search-input',
  inheritAttrs: false,

  props: {
    label: {
      type: String,
    },
    id: {
      type: String,
    },
    value: {
      type: String,
      default: '',
    },
  },

  methods: {
    updateValue(value) {
      this.$emit('input', value);
    },
  }
};
</script>

